export function updateIframeColors(primary, accent, download, black, white, success, error, info, warning, contrasts, fontRendering,customs, customLabels, grLabels, grIds, grValues, label) {
    colorPrimary = primary.slice();
    colorAccent = accent.slice();
    colorDownload = download.slice();
    colorBlack = black.slice();
    colorWhite = white.slice();
    colorSuccess = success.slice();
    colorError = error.slice();
    colorInfo = info.slice();
    colorWarning = warning.slice();
    colorContrasts = contrasts.slice();
    colorCustoms = customs.slice();
    customColorLabels = customLabels;
    gradientColorIds = grIds.slice();
    gradientCustoms = grLabels.slice();
    gradientColorValues = grValues.slice();
    $('.js-cd-demo-container--colors').trigger('updateColors', [label]);
};


export function updateIframeColorLinks(links) {
    $('.js-cd-demo-container--colors').trigger('updateColorLinks', [links]);
}
;

export function updateIframeColorsNewColor(array, title, customLabel) {
    if (title.indexOf('gradient') > -1) {
        newGradientCustom[customLabel] = array.slice();
        $('.js-cd-demo-container--colors').trigger('createGradient', [title, customLabel]);
    } else {
        newColorCustom[customLabel] = array.slice();
        $('.js-cd-demo-container--colors').trigger('createColor', [title, customLabel]);
    }
}
;

export function updateIframeColorsRemoveColor(customLabel) {
    if (customLabel.indexOf('gr') > -1) {
        $('.js-cd-demo-container--colors').trigger('removeGradient', [customLabel]);
    } else {
        $('.js-cd-demo-container--colors').trigger('removeColor', [customLabel]);
    }
}
;

export function updateIframeColorsEditColor(section, title) {
    if (section.indexOf('gradient') > -1) {
        $('.js-cd-demo-container--colors').trigger('updateGradient', [section, title]);
    } else {
        $('.js-cd-demo-container--colors').trigger('updateColor', [section, title]);
    }
}
